// import logo from './logo.svg';
import "./App.css";
import { BrowserRouter as Router, Link } from "react-router-dom";

function Footer() {
  return (
    <>
      <footer
        className="page-footer font-small stylish-color-dark pt-4 bg-dark text-light"
        // style="z-index: 10"
      >
        <div className="container text-center text-md-left">
          <div className="row">
            <div className="col-md-4 mx-auto">
              <h5 className="font-weight-bold text-uppercase mt-3 mb-4">
                Footer Content
              </h5>
              <p>
                Here you can find the softwares. Get most of the Windows
                Softwares, Android Apps with Categories
              </p>
            </div>
            <hr className="clearfix w-100 d-md-none" />
            <div className="col-md-3 mx-auto">
              <h5 className="font-weight-bold text-uppercase mt-3 mb-4">
                Useful Links
              </h5>
              <ul className="list-unstyled">
                <li>
                  <Link to="/">Home</Link>
                </li>
                <li>
                  <Link to="/windows-softwares">Softwares</Link>
                </li>
                <li>
                  <Link to="/apps">Apps</Link>
                </li>
                <li>
                  <Link to="/Categories">Categories</Link>
                </li>
              </ul>
            </div>
            <hr className="clearfix w-100 d-md-none" />
            <div className="col-md-2 mx-auto">
              <h5 className="font-weight-bold text-uppercase mt-3 mb-4">
                Categories
              </h5>
              <ul className="list-unstyled">
                <li>
                  <Link to="/link1">Link 1</Link>
                </li>
                <li>
                  <Link to="/link2">Link 2</Link>
                </li>
                <li>
                  <Link to="/link3">Link 3</Link>
                </li>
                <li>
                  <Link to="/link4">Link 4</Link>
                </li>
              </ul>
            </div>
            <hr className="clearfix w-100 d-md-none" />
          </div>
        </div>
        <hr />
        <div className="footer-copyright text-center py-3">
          &copy; 2021 Copyright:
          <Link to="https://mdbootstrap.com/"> MDBootstrap.com</Link>
        </div>
      </footer>
    </>
  );
}

export default Footer;
