// import logo from './logo.svg';
// import "./App.css";
import { Link } from "react-router-dom";

const Downmanager = [
    {
        Name:"Eagle Get",
        img:"/media/images/100x100/dmanager/eagleget.png",
        To:"/categories/download-manager/eagle-get"
    },
    {
        Name:"FDM",
        img:"/media/images/100x100/dmanager/fdm.jpg",
        To:"/categories/download-manager/fdm"
    },
    {
        Name:"IDM",
        img:"/media/images/100x100/dmanager/idm.png",
        To:"/categories/download-manager/idm"
    },
    {
        Name:"JDownloader",
        img:"/media/images/100x100/dmanager/jdownloader.webp",
        To:"/categories/download-manager/j-downloader"
    },
]

function ncard(val) {
    return (
        <li><Link to={val.To} className="list-group-item list-group-item-action"><img src={val.img} alt={val.Name} /><br />{val.Name}</Link></li>)
}

function DManager() {
    return (
        <>
            <div>
                <div className="cat">
                    <div className="mb-5">
                        <div className="list-group mt-3">
                            <button className="btn btn-success d-block p-2">
                                Download Manager
                </button>
                            <div className="card card-body">
                                <div className="list-group folder-group">
                                    <div className="row no_ls">
                                        {Downmanager.map(ncard)}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default DManager;