import Data from "./Apps.json";
import React from "react";
import { Link } from "react-router-dom";
import './media/css/software.css'

function Application() {
    return (
        <div>
            <div className="cat">
                <div className="mb-5">
                    <div className="list-group mt-3">
                        <button className="btn btn-success d-block p-2">
                            Application
                </button>
                        <div className="card card-body">
                            <div className="list-group folder-group">
                                <div className="row no_ls">{Data.map(post => {
                                    return <li><Link to={post.To} className="list-group-item list-group-item-action"><img src={post.img} alt={post.Name} /><br />{post.Name}</Link></li>
                                }
                                )}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Application;