// import logo from './logo.svg';
// import "./App.css";
import { Link } from "react-router-dom";

const Antivirus = [
    {
        Name: "Avast",
        img: "/media/images/100x100/avast.jpg",
        To: "/categories/antivirus/avast"
    },
    {
        Name: "AVG",
        img: "/media/images/100x100/avg.jpg",
        To: "/categories/antivirus/avg"
    },
    {
        Name: "Avira",
        img: "/media/images/100x100/avira.jpg",
        To: "/categories/antivirus/avira"
    }
]

function ncard(val) {
    return (
        <li><Link to={val.To} className="list-group-item list-group-item-action"><img src={val.img} alt={val.Name} /><br />{val.Name}</Link></li>)
}

function Antiviruses() {
    return (
        <>
            <div>
                <div className="cat">
                    <div className="mb-5">
                        <div className="list-group mt-3">
                            <button className="btn btn-success d-block p-2">
                                Antivirus
                </button>
                            <div className="card card-body">
                                <div className="list-group folder-group">
                                    <div className="row no_ls">
                                        {Antivirus.map(ncard)}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Antiviruses;